				}).then(res => {
					console.log("postLikeComment", res);
				})
			} else {
				this.commentList[reply_flag].like_num--;
				api.postCancelLikeComment({
					did: this.commentList[reply_flag].did
				}).then(res => {
					console.log("postLikeComment", res);
				})
			}

		},
